body{
    margin: 0;
    box-sizing: border-box;
    background-color: #F5F7FB;
   
}
main{
    text-align: center;
    background: url("./blob 1.png")right top no-repeat,url("./blob 2.png") left bottom no-repeat ;
}
.start--screen{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.start--h1{
    margin: 250px 70px 0;
    font-family: 'Karla', sans-serif;
    font-size: 31.25px;
    color: #293264;
    margin-bottom: 0;
}
.start--p{
    font-family: 'Inter';
    text-align: center;
    color: #293264;
    margin-bottom: 0;
    margin-top: 7px;
}
.start--button{
    width: 193px;
    height: 52px;
    background: #4D5B9E;
    border-radius: 15px;
    border-style: hidden;
    font-family: 'Inter';
    font-size: 16px;
    color: #F5F7FB;
    margin-top: 29px;
    cursor: pointer;
    margin-bottom: 353px;
}
.quiz-section{
    padding-top: 50px;
    height: 93vh;
    
}
.quiz--screen{
    display: flex;
    flex-direction: column;
    text-align: left;
    margin-top: 20px;
    margin-left: 20%;
    margin-right: 20%;
    
}
.quiz--question{
    margin-top: 0;
    font-family: 'Karla', sans-serif;
    font-weight: 700;
    font-size: 19px;
    color: #293264;
    margin-bottom: 0;
}

hr{
    width: 400px;
    margin-top: 15px;
    margin-bottom: 0;
    margin-left: 0;
}
.quiz--button{
    width: 193px;
    height: 52px;
    background: #4D5B9E;
    border-radius: 15px;
    border-style: hidden;
    font-family: 'Inter';
    font-size: 20px;
    color: #F5F7FB;
    margin-top: 35px;
    cursor: pointer;
}

.answer{
    margin-top:13px ;
    background: #F5F7FB;
    border-radius: 8px;
    padding: 4px 10px;
    margin-right: 13px;
    border-color: #4D5B9E;
    cursor: pointer;
}
.selected{
    background-color: #D6DBF5;
}
#correct.answer{
    background-color: #94D7A2;
    border: 1px solid #94D7A2;
}
#incorrect.answer.selected{
    background-color: #F8BCBC;
    border: 1px solid #F8BCBC;
}
#not-selected.answer{
    color: rgba(41, 50, 100, .4);
    border: 1px solid rgba(41, 50, 100, .4);
}
.score{
    font-family: 'Inter';
    font-weight: 700;
    font-size: 1rem;
    padding-right: 15px;
}
@media (max-width: 684px){
    .quiz-section{
        height: 112vh;  
    }
    .quiz--question{
        font-size: 16px;
    }
 
}
@media (min-height: 1200px){
    .start--h1{
        margin-top:70%;
        font-size: 90px;
    }
    .start--p{
        font-size: 40px;
    }
    .start--button{
        font-size: 40px;
        width: 240px;
        height:80px;
        margin-bottom: 162%;
    }
    .answer{
        display: flex;
        font-size: 27px;
        
    }
    .quiz--question{
        
        font-size: 30px;
    }
    .quiz--button{
        width: 250px;
        height: 70px;
        font-size: 35px;
        margin-bottom: 5%;
    }
    .score{
        font-size: 37px;
        padding-right: 30px;
    }
    .quiz-section{
        height: 108vh;  
    }
}

